export const Head = () => (
  <Seo
    title="Justtry Technologies Blog: Updates, news, Articles"
    headTitle="Justtry Technologies Blog: Updates, news, Articles"
    description="Dive into the world of innovation with Justtry Technologies' blog. Uncover the latest tech trends, gain insights from industry experts, and empower yourself with cutting-edge knowledge. Explore a fusion of expertise and creativity as we unravel the future of technology together. Justtry Technologies - where curiosity meets innovation."
    keywords="Blog, Keywords, Top 10, Tips, How-to, Guide, Insights, Trends, Best Practices, Expert advice"
  ></Seo>
);
export default BlogsScreen;

export const query = graphql`
  query MyQuery {
    allImageSharp {
      edges {
        node {
          gatsbyImageData
          id
        }
      }
    }
    allDataJson {
      edges {
        node {
          id
          keyId
          slug
          author_name
          blog_created_date
          title
          short_description
          description
          meta_title
          meta_description
          image
          category
          tags
        }
      }
      pageInfo {
        perPage
        hasNextPage
      }
    }
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
        siteUrl
      }
    }
  }
`;
